import {List, SimpleListItem} from '@rmwc/list'
import '@rmwc/list/styles';

function ListContainer( {schedule, info, periods} ){
    return (
        <List twoLine>
            {
                schedule[1].map((a, b) => {
                    return (
                        <SimpleListItem
                            graphic={info[a].icon}
                            text={a}
                            secondaryText={periods[b]}
                        />
                    )
                })
            }

        </List>
    )
}

export default ListContainer